import { FC } from 'react';
import { createGlobalStyle, css } from 'styled-components';

import {
  MEDIA,
  DEVICES,
  GUTTER,
  COLUMNS,
  GRID,
  CSS_GRID,
  CSS_COLUMNS,
  CSS_GUTTER,
  CSS_COLUMN_WIDTH,
} from '#components/Layout/Grid/Grid.constants';

/**
 * @todo create a helper for generating these. Alternatively, consider moving
 * the grid vars to the :root scope.
 */
const GlobalStyles = createGlobalStyle`
  :root {
      ${CSS_GUTTER}: ${GUTTER}px;
      ${CSS_COLUMNS}: ${COLUMNS.mobile};
      ${CSS_GRID}: ${GRID.mobile};
      ${CSS_COLUMN_WIDTH}: calc(var(${CSS_GRID}) / var(${CSS_COLUMNS}));

      ${() =>
        DEVICES.map((device) => {
          const media = MEDIA[device];
          const grid = GRID[device];
          const columns = COLUMNS[device];

          return css`
            @media ${media} {
              ${CSS_COLUMNS}: ${columns};
              ${CSS_GRID}: ${typeof grid === 'number' ? `${grid}px` : grid};
            }
          `;
        })}
    }
`;

/**
 * 'storybook-addon-grid' allows for configuration of the grid overlay by means
 * of CSS custom properties. These, however, are not being applied to the :root
 * scope by the `<Grid />` component. This decorator applies the grid variables
 * to the :root scope for the purposes o the stories.
 */
const withGrid = (Story: FC) => (
  <>
    <GlobalStyles />
    <Story />
  </>
);

export default withGrid;
